<template>
  <div class="container">
    <mt-header title="">
      <router-link to="/" slot="left">
        <mt-button icon="back"></mt-button>
      </router-link>
    </mt-header>

    <mt-field placeholder="" type="tel" v-model="phone" autocomplete="off"></mt-field>
    <mt-field placeholder="" type="password" v-model="password" autocomplete="off"></mt-field>
    <div class="btn">
      <mt-button @click.native="login" class="btncolor"></mt-button>
    </div>
  </div>
</template>

<script>
import Vue from 'vue'
import axios from 'axios'
import { Header, Field, Button, Toast } from 'mint-ui'
Vue.use(Header, Field, Button, Toast)

export default {
  name: 'register',
  data () {
    return {
      msg: '',
      phone: '18895379820',
      password: ''
    }
  },
  computed: {
  },
  methods: {
    login () {
      axios.post('https://www.daxunxun.com/users/login', {
        username: this.phone,
        password: this.password
      }).then(data => {
        // switch (data.data) {
        //   case 0: Toast(''); break
        //   case 1: Toast(''); break
        //   case 2: Toast(''); break
        //   case -1: Toast(''); break
        // }
        if (data.data === 1) {
          let storage = window.localStorage
          storage.setItem('isLogin', 'ok')
          this.$router.push('/')
          Toast({
            message: '!',
            position: 'bottom',
            duration: 500
          })
        } else if (data.data === 0) {
          Toast({
            message: '',
            position: 'bottom',
            duration: 500
          })
        } else if (data.data === -1) {
          Toast({
            message: '',
            position: 'bottom',
            duration: 500
          })
        } else {
          Toast({
            message: '',
            position: 'bottom',
            duration: 500
          })
        }
      })
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss">
  .mint-header{
    background-color: #333333;
  }
  .mint-field {
    input:-webkit-autofill { box-shadow: 0 0 0px 1000px white inset;}
  }
  .btn{
    margin-top: .2rem;
    text-align: center;
  }
  .btncolor{
    background-color: #333333;
    color: #fff;
  }
</style>



